import UploadFile from '@/components/UploadFile/UploadFile.vue';
import { createPackage, getAppList } from '@/api/peoplePackage'; // var moment = require('moment')

export default {
  name: 'Tab',
  components: {
    UploadFile: UploadFile
  },
  data: function data() {
    return {
      app: '',
      appList: [],
      form: {
        name: '',
        type: '2',
        pl: 0,
        customer_id: 0,
        appid: 0,
        p_path: '',
        p_num: '0',
        exid: 0,
        remarks: '',
        over: '0'
      },
      editor: false,
      uploading: false,
      successName: '文件拖入此处或者点击',
      isAdmin: true,
      backroute: 'peoplepackmanage'
    };
  },
  created: function created() {
    var _this = this;

    if (this.$route.params.packageInfo) {
      this.editor = true;
      this.form = this.$route.params.packageInfo;
      this.form.over = '0';
      console.log(this.form);
    }

    if (this.$route.path === '/peoplePackage/upload1') {
      this.isAdmin = false;
      this.form.pl = 1;
      this.backroute = 'peoplepackage';
    }

    if (!this.isAdmin && this.editor) {
      this.successName = '重新上传';
    }

    getAppList({}).then(function (res) {
      _this.appList = res.data;

      _this.appList.forEach(function (item, index) {
        if (item.appid === _this.form.appid) {
          _this.app = index;
        }
      });
    });
  },
  methods: {
    selectApp: function selectApp(e) {
      this.form.appid = this.appList[e].appid;
      this.form.customer_id = this.appList[e].cus_id;
    },
    submit: function submit() {
      var _this2 = this;

      var msg = this.form.id ? '人群包修改成功' : '人群包创建成功';
      createPackage(this.form).then(function (res) {
        if (res.data === true) {
          _this2.$message({
            message: msg,
            type: 'success'
          });

          _this2.$router.push({
            name: _this2.backroute
          });
        }
      });
    },
    cancel: function cancel() {
      this.$router.push({
        name: this.backroute
      });
    },
    handleSuccess: function handleSuccess(status, result) {
      if (status) {
        this.uploading = false;
        this.form.p_num = result.num;

        if (!this.isAdmin) {
          this.form.p_path = result.fname;
        } else {
          this.form.exid = result.exid;
          this.form.p_path = result.fname;
        }

        this.successName = '文件上传成功';
        this.$message({
          message: '上传成功',
          type: 'success'
        });
      } else {
        this.uploading = true;
      }
    }
  }
};