import request from '@/utils/request'; // import localRequest from '@/utils/localRequest'

export function uploadFile(data) {
  return request({
    url: '/app/file/sliceUpload',
    timeout: 120000,
    // request timeout
    method: 'post',
    data: data
  });
}
